<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox @searchSrldId="searchSrldId" style="width: calc(100% - 120px);float: left;"></QueryBox>
      <div class="queryBtns">
        <Button type="primary" size="small" icon="md-cloud-download" @click="handleExport" class="btn-exportBtn">批量导出</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight">
        </Table>
        <div class="page_box">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QueryBox from './areaQueryBox'
import {
  // 表格列表
  redLineReport,
  // 导出
  exportFile
} from '@/api/achievement'
export default {
  name: 'achievementTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          // fixed: 'left',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '行政区划',
          align: 'center',
          // ellipsis: true,
          // tooltip: true,
          // fixed: 'left',
          minWidth: 200,
          key: 'adminRegion'
        },
        {
          title: '斑块编号',
          align: 'center',
          // fixed: 'left',
          minWidth: 120,
          key: 'pointNumber'
        },
        {
          title: '斑块名称',
          align: 'center',
          minWidth: 120,
          // ellipsis: true,
          // tooltip: true,
          key: 'pointName'
        },
        {
          title: '原斑块活动类型名称',
          align: 'center',
          minWidth: 120,
          // ellipsis: true,
          // tooltip: true,
          key: 'orignType'
        },
        {
          title: '现在斑块活动类型名称',
          align: 'center',
          minWidth: 120,
          // ellipsis: true,
          // tooltip: true,
          key: 'nowType'
        },
        {
          title: '原斑块面积(亩)',
          align: 'center',
          minWidth: 120,
          key: 'orignScale'
        },
        {
          title: '现在斑块面积(亩)',
          align: 'center',
          minWidth: 120,
          key: 'nowScale'
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 查询框返回查询条件 */
      obj: '',
      comCode: '' // 区划
    }
  },
  components: {
    QueryBox
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  methods: {
    /* 查询框 */
    searchSrldId (obj) {
      this.comCode = obj.code
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      redLineReport({
        data: {
          pageSize: this.pageSize,
          pageNumber: this.pageNum,
          comCode: this.comCode
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data
          this.total = res.data.data.length
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* 导出文件 */
    handleExport () {
      exportFile({
        data: {
          comCode: this.comCode
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          let data = res.data.data
          if (!data) {
            return
          }
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = res.data.data.filepath
          link.href = '/epr' + url
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .ml20 {
      margin-left: 20px;
    }
    .mr20 {
      margin-right: 20px;
    }
    .mt15 {
      margin-top: 15px;
    }
    .pull-right {
      float: right;
    }
    .btn-tabBtn {
      background: rgb(16,186,204);
      border-color: rgb(16,186,204);
      margin-right: 10px;
      &:hover {
        background: #13B1CC;
        border-color: #13B1CC;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
      &.icon-leimupinleifenleileibie {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .achievementAddActive{
      right:0;
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
  }
</style>
